import time from "./time";

export default {
  init: `
    .page-transition-enter {
    }
    .page-transition-enter-active {
    }
    .page-transition-exit {
    }
    .page-transition-exit-active {
    }
    .page-transition-enter #horizonStatics{
    }
  `,
  slideAsVerticalNext: `
    .page-transition-enter {
      transform: translateY(100%);
      #horizonStatics {
        transform: translateY(-100%);
      }
    }

    .page-transition-enter-active {
      animation: slideAsVerticalNext ${ time.transitionAnimation }ms;
      @keyframes slideAsVerticalNext {
        0% {
          transform: translateY(100%);
          animation-timing-function: cubic-bezier(0.22, 1, 0.36, 1);
        }
        50% {
          transform: translateY(-5%);
          
          animation-timing-function: ease-in;
        }
        100% {
          transform: translateY(0);
          animation-timing-function: ease-out;
        }
      }

      #horizonStatics {
        animation: slideAsVerticalNext2 ${ time.transitionAnimation }ms;
        opacity: 0;
      }
    }

    .page-transition-exit {
      transform: translateY(0);
      #horizonStatics {
        transform: translateY(0);
      }
    }

    .page-transition-exit-active {
      transform: translateY(-100%);
      transition: transform ${ time.transitionAnimation }ms cubic-bezier(0.32, 0, 0.67, 0);
      #horizonStatics {
        transform: translateY(100%);
        transition: transform ${ time.transitionAnimation }ms cubic-bezier(0.32, 0, 0.67, 0);
      }
    }
  `,
  slideAsVerticalPrev: `
    .page-transition-enter {
      transform: translateY(-100%);
      #horizonStatics {
        transform: translateY(0);
      }
    }

    .page-transition-enter-active {
      transform: translateY(0);
      transition: transform ${ time.transitionAnimation }ms cubic-bezier(0.32, 0, 0.67, 0);
      #horizonStatics {
        transform: translateY(-100%);
        transition: transform ${ time.transitionAnimation }ms cubic-bezier(0.32, 0, 0.67, 0);
      }
    }

    .page-transition-exit {
      transform: translateY(100%);
      #horizonStatics {
        transform: translateY(100%);
      }
    }

    .page-transition-exit-active {
      animation: slideAsVerticalPrev ${ time.transitionAnimation }ms;
      @keyframes slideAsVerticalPrev {
        0% {
          transform: translateY(0);
          animation-timing-function: cubic-bezier(0.22, 1, 0.36, 1);
        }
        50% {
          transform: translateY(-5%);
          animation-timing-function: ease-in;
        }
        100% {
          transform: translateY(100%);
          animation-timing-function: ease-out;
        }
      }

      #horizonStatics {
        animation: slideAsVerticalPrev2 ${ time.transitionAnimation }ms;
      }
    }
  `,
  slideAsHorizontalNext: `
    .page-transition-enter {
      transform: translateX(100vw);
      #horizonStatics {
        transform: translateX(-100vw);
      }
    }

    .page-transition-enter-active {
      animation: slideAsHorizontalNext ${ time.transitionAnimation }ms;
      @keyframes slideAsHorizontalNext {
        0% {
          transform: translateX(100vw);
          animation-timing-function: cubic-bezier(0.22, 1, 0.36, 1);
        }
        50% {
          transform: translateX(-5vw);
          
          animation-timing-function: ease-in;
        }
        100% {
          transform: translateX(0);
          animation-timing-function: ease-out;
        }
      }

      #horizonStatics {
        animation: slideAsHorizontalNext2 ${ time.transitionAnimation }ms;
        @keyframes slideAsHorizontalNext2 {
          0% {
            transform: translateX(-100vw);
            animation-timing-function: cubic-bezier(0.22, 1, 0.36, 1);
          }
          50% {
            transform: translateX(5vw);
            
            animation-timing-function: ease-in;
          }
          100% {
            transform: translateX(0);
            animation-timing-function: ease-out;
          }
        }
      }
    }

    .page-transition-exit {
      transform: translateX(0);
      #horizonStatics {
        transform: translateX(0);
      }
    }

    .page-transition-exit-active {
      transform: translateX(-100vw);
      transition: transform ${ time.transitionAnimation }ms cubic-bezier(0.32, 0, 0.67, 0);
      #horizonStatics {
        transform: translateX(100vw);
        transition: transform ${ time.transitionAnimation }ms cubic-bezier(0.32, 0, 0.67, 0);
      }
    }
  `,
  slideAsHorizontalPrev: `
    .page-transition-enter {
      transform: translateX(-100vw);
      #horizonStatics {
        transform: translateX(100vw);
      }
    }

    .page-transition-enter-active {
      animation: slideAsHorizontalPrev ${ time.transitionAnimation }ms;
      @keyframes slideAsHorizontalPrev {
        0% {
          transform: translateX(-100vw);
          animation-timing-function: cubic-bezier(0.22, 1, 0.36, 1);
        }
        50% {
          transform: translateX(5vw);
          
          animation-timing-function: ease-in;
        }
        100% {
          transform: translateX(0);
          animation-timing-function: ease-out;
        }
      }

      #horizonStatics {
        animation: slideAsHorizontalPrev2 ${ time.transitionAnimation }ms;
        @keyframes slideAsHorizontalPrev2 {
          0% {
            transform: translateX(100vw);
            animation-timing-function: cubic-bezier(0.22, 1, 0.36, 1);
          }
          50% {
            transform: translateX(-5vw);
            
            animation-timing-function: ease-in;
          }
          100% {
            transform: translateX(0);
            animation-timing-function: ease-out;
          }
        }
      }
    }

    .page-transition-exit {
      transform: translateX(0);
      #horizonStatics {
        transform: translateX(0);
      }
    }

    .page-transition-exit-active {
      transform: translateX(100vw);
      transition: transform ${ time.transitionAnimation }ms cubic-bezier(0.32, 0, 0.67, 0);
      #horizonStatics {
        transform: translateX(-100vw);
        transition: transform ${ time.transitionAnimation }ms cubic-bezier(0.32, 0, 0.67, 0);
      }
    }
  `
}
